.section-about {
    background-color: $color-grey-light-1;
    padding: 25rem 0;
    margin-top: -20vh;
}

.section-features {
    background-image: linear-gradient(
        to right bottom,
        rgba($color-primary-light, .8),
        rgba($color-primary-dark, .8)
    ), url('../../img/nat-4.jpg');
    padding: 20rem 0;
    transform: skewY(-7deg);
    margin-top: -12rem;
    
    & > * {
        transform: skewY(7deg);
    }
}


.section-tours {
    background-color: $color-grey-light-1;
    padding: 25rem 0 15rem 0;
    margin-top: -12rem;
}

.section-stories {
    padding: 20rem 0;
    position: relative;
    min-height: 1200px;
}

.section-booking {
    background-image: linear-gradient(
        to right bottom,
        $color-primary-light,
        $color-primary-dark
    );
    padding: 15rem 0;
}

.book {
    // background-image: linear-gradient(
    //     105deg,
    //     rgba($color-white, .9) 0%,
    //     rgba($color-white, .9) 50%,
    //     transparent 50%
    // ), url('../img/nat-10.jpg');
    background-size: cover;
}