.composition {
    position: relative;

    &__photo {
        width: 55%;
        outline-offset: 2rem;
        box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
        border-radius: .2rem;
        transition: all .2s;
        position: absolute;
        z-index: 10;

        &:hover {
            outline: 1.5rem solid $color-primary;
            box-shadow: 0 2.5rem 4rem rgba($color-black, .4);
            transform: scale(1.05) translateY(-.5rem);
            z-index: 20;
        }

        &--p1 {
            top: -2rem;
            left: 0;
        }

        &--p2 {
            top: 2rem;
            right: 0;
        }

        &--p3 {
            top: 10rem;
            left: 20%;
        }

    }

    &:hover &__photo:not(:hover) {
        transform: scale(.9);
    }
}